@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




.home-page {
  display: flex;
  flex-flow: row nowrap;
  padding: 15px;
}

.items-display {
  display: flex;
  flex-flow: column wrap;
  width: 100%;
  align-items: center;
}

.search-container {
  display: flex;
  flex-direction: column;
  width: 90%;
}


.side-bar {
  border-width: 1px;
  border-color: rgb(0,0,0);
  border-style: solid;
  padding-bottom: 15px;
  width: 33%;
}

.category-container {
  margin-top: 15px;
}

.side-bar h3 {
  text-align: center;
}
.form-input-section {
  display: flex;
  flex-flow: column nowrap;
  width: 60%;
  margin: 10px 0;
}

.form-inputs {
  margin: 7px 0;
}

.email-and-stars {
  display: flex;
  flex-flow: row nowrap;
  width: 100%;
  align-items: center;
}

.star-rating {
  margin: auto;
}

.star-rating div {
  margin: 0 5px;
}

.star-rating .fa-star {
  color: rgba(0, 0, 0, 0.350);
}

.star-rating .checked {
  color: rgb(218, 165, 32);
}

#email {
  width: 40%
}

#message {
  height: 40vh;
}

.form-button {
  width: 15%;
  padding: 10px;
  margin: 1% 1% 1% 0;

}
.rating-history {
  display: flex;
  flex-flow: column nowrap;
  border: 1px solid black;
  margin: 10px 10px;
  padding: 10px;
}

.rating-column {
  display: flex;
  flex-flow: row nowrap;
  width: 60%;
  margin-bottom: 30px;
  justify-content: space-between;
  align-items: center;
}

.rating {
  margin: auto;
}
